import { isCollection } from "@tsed/core";
/**
 * @ignore
 */
export class JsonMapperContext {
    collectionType;
    type;
    options;
    _next;
    constructor({ type, collectionType, next, options }) {
        this.type = type;
        this.collectionType = isCollection(collectionType) ? collectionType : undefined;
        this._next = next;
        this.options = options;
    }
    next(data) {
        return this._next(data, {
            ...this.options,
            type: this.type
        });
    }
}
